import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { SearchModule } from 'src/app/common-module/search/search.module';
import { BtnAddModule } from 'src/app/common-module/btn-add/btn-add.module';
import { TableListModule } from 'src/app/common-module/table-list/table-list.module';
import { AddBayComponent } from './add-bay/add-bay.component';
import { BayListComponent } from './bay-list/bay-list.component';
import { NgSelectModule } from '@ng-select/ng-select';
import { SharedModule } from 'src/app/shared/shared.module';
import { FormValidationModule } from 'src/app/shared/form-validation/form-validation.module';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { PaginationModule } from 'src/app/common-module/pagination/pagination.module';
import { BayService } from 'src/app/service/bay.service';
import { BayViewComponent } from './bay-view/bay-view.component';

const routes: Routes = [
  {
    path: "",
    component: BayListComponent,
    data: { title: 'bay_list' }
  },
  {
    path: "add",
    component: AddBayComponent,
    data: { title: 'add_bay' }
  },
  {
    path: 'edit/:id',
    component: AddBayComponent,
    data: { title: 'edit_bay' }
  },
  {
    path: 'view/:id',
    component: BayViewComponent,
    data: { title: 'view_bay' }
  }
]

@NgModule({
  declarations: [AddBayComponent, BayListComponent, BayViewComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    TableListModule,
    SearchModule,
    BtnAddModule,
    FormsModule,
    ReactiveFormsModule,
    FormValidationModule,
    SharedModule,
    NgSelectModule,
    PaginationModule
  ], providers: [BayService]
})

export class BayModule { }

