
import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { BayService } from 'src/app/service/bay.service';


@Component({
  selector: 'app-bay-view',
  templateUrl: './bay-view.component.html'
})
export class BayViewComponent implements OnInit {

  viewId: any;
  objectArray: any;
  popup: Window;

  constructor(
    private activatedRoute: ActivatedRoute,
    private bayservice: BayService
  ) {
    if (this.activatedRoute.snapshot.paramMap.get('id')) {
      this.viewId = this.activatedRoute.snapshot.paramMap.get('id');
    }
  }
  ngOnInit(): void {
    this.bayservice.getBayById(this.viewId).subscribe((response) => {
      if (response.success) {
        this.objectArray = response.data;

      }
    });
  }

  VoucherSourcetoPrint() {
    return (
      `
              <html>
              <head>
                <script>
                  function step1() {
                    setTimeout('step2()', 10);
                  }
                  function step2() {
                    window.print(); window.close()
                  }
                </script>
              </head>

              <body onload='step1()'>
                <h3>Bay Name:- ${this.objectArray?.bay_name}</h3>
                <h3>Bay Code:- ${this.objectArray?.bay_code}</h3>
                <img src='${this.objectArray?.bay_bar_code_image}' />
              </body>
              </html>
        `
    );
  }
  VoucherPrint() {
    // let pwa = window.open();
    // pwa.document.open();
    // pwa.document.write(this.VoucherSourcetoPrint());
    // pwa.document.close();
    window.print();
  }


}

