import { Component, OnInit } from '@angular/core';
import { PaginationService } from 'src/app/service/pagination.service';
import { TextField, ActionField } from 'src/app/common-module/table-list/list-field-type';
import { BayService } from 'src/app/service/bay.service';

@Component({
  selector: 'app-bay-list',
  templateUrl: './bay-list.component.html'
})
export class BayListComponent implements OnInit {

  action: any;
  loadform = false;
  loadingState = true;
  objectArray: Array<any> = [];
  pagination: any = null;
  searchText: any = null;
  currentPage: any = 1;
  permissionObject: any = null;
  showPagination: boolean = false;
  PageTitle = "Manage Bay";

  //For Dynamic List
  headerData: Array<any> = [];

  constructor(
    private paginationService: PaginationService,
    private bayservice: BayService,
  ) { }

  ngOnInit() {
    // this.dataService.permission.subscribe((perms) => {
    //   this.permissionObject = perms['self'];
    // });
    this.loadform = false;
    this.setHeaderData();
    this.getObjects();
  }
  setHeaderData() {
    this.headerData.push(
      new TextField({
        label: "Bay Name",
        key: "bay_name",
      }),
      new TextField({
        label: "Bay Code",
        key: "bay_code",
      }) ,
      new TextField({
        label: "Bar Code No",
        key: "bay_bar_code",
      }),
    );
    let canEdit = true;
    let canView = true;
    let canDelete = true;
    let actions = {};
    if (canEdit) {
      actions['edit'] = {
        path: '/manage/bay/edit/',
        id: 'bay_id'
      }
    }
    if (canView) {
      actions['view'] = {
        path: '/manage/bay/view/',
        id: 'bay_id'
      }
    }
    if (canDelete) {
      actions['delete'] = {
        path: 'bay/',
        id: 'bay_id'
      }
    }
    let actionColom = new ActionField(actions);
    this.headerData.push(
      actionColom
    );
  }

  getObjects() {
    const params: any = { page: this.currentPage };
    if (this.searchText) {
      params.search = this.searchText;
    }
    this.bayservice.getBayList(params).subscribe((response) => {
      this.loadingState = false;
      if (response.success) {
        this.objectArray = response.data.list;
        this.showPagination = true;
        this.pagination = this.paginationService.getPager(response.data.pagination['total_page'], this.currentPage);
      } else {
        this.objectArray = [];
        this.pagination = null;
      }
    }, (error) => {
      this.loadingState = false;
      this.objectArray = [];
      this.pagination = null;
    });
  }

  getPage(page: number) {
    this.currentPage = page;
    this.getObjects();
  }

  searchObject(text) {
    this.searchText = text;
    this.currentPage = 1;
    this.getObjects();
  }
  onReloadEvent() {
    this.getObjects();
  }
}




